@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap');

* {
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  margin: 0px;
  padding: 0px;

}

.formDiv {
  min-height: 100vh;
  /* background: #2a2929; #2a2929 */
  background: black;
}

.heading {
  color: #45ed4e;
  position: absolute;
  top: 10px;
  font-size: 30px;
  margin-top: 20px;
}

/* style={{ textAlign: "center"}} */
.form_wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 80px 0px;
  border: 10px 10px;
  border-radius: 5px;
}

.form_comp {
  width: 50%;
  justify-content: center;
  padding: 10px;
  margin: 0px 35px;
}

label {
  color: aliceblue;

}

input,
select {
  width: 100%;
  height: 40px;
  /* text-align: center;    */
  padding: 12px 20px;
  margin: 8px 0;
  display: flex;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.submit {
  width: 100%;
  background-color: #45ed4e;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: 0.2s all;
}

.submit:hover {
  background-color: #4CAF50;
}


.submit:active {
  background-color: #45ed4e;
}

.showError {
  border-color: #a94442 !important;
  color: #a94442 !important;
  margin-bottom: 15px;
}


@media screen and (max-width: 900px) {
  .form_comp {
    width: 68%;
  }
}


@media screen and (max-width: 700px) {
  .form_comp {
    width: 70%;
  }
}

@media screen and (max-width: 550px) {
  .form_comp {
    width: 80%;
  }
}

@media screen and (max-width: 475px) {
  .form_comp {
    width: 90%;
  }
}


.copy{
  color: #45ed4e;
  text-align: center;
}